<template>
	<view class="tab">
		<view class="tab-select">
			<view class="tab-item" v-for="(item,index) in select" @click="active(index)" :class="{active:index==num}">
				{{item.title}}
			</view>
		</view>
		<view class="tab-content" v-for="(item,index) in content" v-show="index==num">
			{{item.title}}
		</view>
	</view>
</template>

<script>
	export default {
		name: "tab",
		data() {
			return {
				select: [
					{
						id: 1,
						title: "推荐"
					},
					{
						id: 2,
						title: "排行榜"
					},
					{
						id: 3,
						title: "mv"
					},
				],
				content:[
					{
						id: 1,
						title: "推荐内容"
					},
					{
						id: 2,
						title: "排行榜内容"
					},
					{
						id: 3,
						title: "mv内容"
					},
				],
				num:0
			};
		},
		methods: {
			active(idx){
				this.num=idx;
			}
		},
	}
</script>

<style lang="less">
.tab{
	.tab-select{
		margin-top: 30rpx;
		width: 80%;
		margin-left: 10%;
		height: 60rpx;
		.tab-item{
			display: inline-block;
			width: 33.33%;
			height: 60rpx;
			line-height: 60rpx;
			text-align: center;
			font-size: 18px;
			color: #f87373;
			font-weight: 400;
			box-sizing: border-box;
			border: 1rpx solid #f87373;	
		}
	}
	.tab-content{
		margin-top: 20rpx;
		width: 100%;
		margin-bottom: 60rpx;
	}
}
.active{
			color: #FFFFFF !important;
			background-color: #f87373;
			
		}
</style>
